  margin: 0px;
}
/* Banner */

.banner {
  display: inline-block;
  background: rgba(0, 0, 0, 0.5);
  color: var(--mainWhite);
  padding: 2rem 1rem;
  text-align: center;
  text-transform: capitalize;
  letter-spacing: var(--mainSpacing);
}

.banner h1 {
  font-size: 2.5rem;
}

.banner div {
  width: 10rem;
  height: 5px;
  background: var(--primaryColor);
  margin: 1.7rem auto;
}

.banner p {
  font-size: 1.5rem;
  margin-bottom: 2rem;
  color: white;
}


/* if min-width < 576px overwrite these properties */

@media screen and (min-width: 576px) {
  .banner {
    padding: 2rem 3rem;
  }
  .banner h1 {
    font-size: 3rem;
  }
}


/* if min-width < 992px overwrite these properties */

@media screen and (min-width: 992px) {
  .banner {
    padding: 2rem 6rem;
  }
  .banner h1 {
    font-size: 4rem;
  }
}


/* End of Banner */
.btn-primary {
  /*overwriteandsetthesepropertiesforthiselement*/
  text-decoration: none;
  /*letter-spacing: var(--mainSpacing);
  NB)icommentthisbecauseafatheralreadyasthisproperty*/
  color: var(--mainBlack);
  background: var(--primaryColor);
  padding: 0.4rem 0.9rem;
  border: 3px solid var(--primaryColor);
  transition: #71c55d;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: #71c55d;
  -moz-transition: #71c55d;
  -ms-transition: #71c55d;
  -o-transition: #71c55d;
}

.btn-primary:hover {
  background: transparent;
  color: var(--primaryColor);
}
