  padding: 0.5em 1em;
  width: 50%;
  box-shadow: -2px 0 2px -1px rgba(0, 0, 0, 0.1);
  height: 200px;
}
.container .blog-posts .post .content:before {
  content: '';
  position: absolute;
  background: white;
  width: 10px;
  height: 10px;
  top: 20%;
  left: -5px;
  transform: rotate(45deg);
  box-shadow: -1px 0 2px -1px rgba(0, 0, 0, 0.1);
}
